html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.d-spacebeetwen {
  display: flex;
  justify-content: space-between;
}

.j-center {
  display: flex;
  justify-content: center;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto Regular.woff2") format("woff2"), url("../fonts/Roboto Regular.woff") format("woff"), url("webfont.ttf") format("truetype"), url("webfont.svg#svgFontName") format("svg");
  /* Legacy iOS */
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto Medium.woff2") format("woff2"), url("../fonts/Roboto Medium.woff") format("woff"), url("webfont.ttf") format("truetype"), url("webfont.svg#svgFontName") format("svg");
  /* Legacy iOS */
  font-style: normal;
  font-weight: 500;
}

@font-face {
  font-family: 'Roboto';
  src: url("../fonts/Roboto Bold.woff2") format("woff2"), url("../fonts/Roboto Bold.woff") format("woff"), url("webfont.ttf") format("truetype"), url("webfont.svg#svgFontName") format("svg");
  /* Legacy iOS */
  font-style: normal;
  font-weight: 700;
}

@font-face {
  font-family: 'Open Sans';
  src: url("../fonts/Open Sans Semibold.woff2") format("woff2"), url("../fonts/Open Sans Semibold.woff") format("woff"), url("webfont.ttf") format("truetype"), url("webfont.svg#svgFontName") format("svg");
  /* Legacy iOS */
  font-style: normal;
  font-weight: 600;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.container--fluid {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 30px;
}

.header__top {
  align-items: center;
  padding: 23px 0 17px;
  font-family: "Roboto";
  font-weight: 400;
  line-height: 24px;
}

.top__text, .top__text-phone {
  margin-left: auto;
  margin-right: 25px;
  font-size: 15px;
  color: #546e7a;
}

.top__text-link, .top__text-phone-link {
  color: #e74c3c;
}

.top__text-link:hover, .top__text-phone-link:hover {
  border-bottom: solid 1px #e74c3c;
}

.top__text-link:focus, .top__text-phone-link:focus {
  outline: solid 2px #bf2718;
}

.top__text-link:active, .top__text-phone-link:active {
  color: #bf2718;
  outline: none;
}

.top__text-phone:hover, .top__text-phone-phone:hover {
  border-bottom: solid 1px #546e7a;
}

.top__text-phone:focus, .top__text-phone-phone:focus {
  outline: solid 2px #35454d;
}

.top__text-phone:active, .top__text-phone-phone:active {
  color: #35454d;
  outline: none;
}

.check {
  font-size: 12px;
  border-radius: 4px;
}

.check__link--login, .check__link--registet {
  padding: 9px 20px;
  color: #fff;
  border-radius: 4px;
  width: 72px;
  height: 32px;
}

.check__link--login {
  background-color: #8BC34A;
}

.check__link--registet {
  background-color: #e74c3c;
}

.check__link--login:hover, .check__link--registet:hover {
  background-color: #fff;
}

.check__link--login:hover {
  color: #8BC34A;
}

.check__link--registet:hover {
  color: #bf2718;
}

.menu {
  background-color: #263238;
  padding: 23px 0;
}

.menu__list-item {
  margin-right: 36px;
  display: inline-block;
}

.menu__list-link, .menu .products__category-link {
  color: #fff;
  line-height: 30px;
  font-size: 14px;
  font-family: "Roboto";
  font-weight: 500;
}

.menu__list-link:hover, .menu__list-link:active {
  border-bottom: 1px solid #fff;
}

.search {
  background: url("../images/bg__search.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.search__inner {
  padding: 135px 0 100px;
  font-family: "Roboto";
  font-weight: 500;
  color: #fff;
  line-height: 30px;
  text-align: center;
}

.search__inner-title {
  font-family: "Roboto";
  font-weight: 700;
  font-size: 48px;
}

.search__inner-text {
  font-size: 20px;
}

.form__search {
  margin-top: 54px;
  position: relative;
}

.form__search-input {
  width: 670px;
  height: 56px;
  border-radius: 4px;
  box-shadow: 0px 1px 10px 0 rgba(124, 178, 64, 0.75);
  background-color: #ffffff;
  font-size: 14px;
  letter-spacing: 0.7px;
  padding: 0 60px 0 25px;
}

.form__search-btn {
  position: absolute;
  border: none;
  background-color: transparent;
  top: 25%;
  right: 280px;
}

.form__search-btn:hover {
  cursor: pointer;
}

.products {
  padding: 121px 0 123px;
  background-color: #f4f6f5;
}

.products__cart {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}

.title--h2 {
  font-family: "Roboto";
  font-weight: 700;
  font-size: 36px;
  line-height: 1.67;
  color: #263238;
  text-align: center;
}

.products__category {
  margin-top: 60px;
}

.products__category-link {
  margin-left: 6px;
  padding: 13px 20px;
  box-shadow: 0px 2px 5px 0 rgba(202, 215, 228, 0.75);
  background-color: #fff;
  font-family: "Roboto";
  font-weight: 500;
  color: #263238;
  border: 0;
  outline: none;
  border-radius: 0;
}

.products__category-link:hover, .products__category-link:active {
  color: #fff;
  background-color: #e74c3c;
}

.cart__info, .trend__info {
  widows: 442px;
  margin-top: 59px;
  margin-right: 16px;
  font-family: "Roboto";
  font-weight: 500;
  color: #263238;
  font-size: 14px;
  border-style: solid;
  border-color: #e7ecf2;
  border-width: 0 1px 1px 1px;
  transition: 0.5s all;
}

.cart__info:hover, .trend__info:hover {
  box-shadow: 0px 16px 20px 0 rgba(216, 216, 216, 0.75);
}

.cart__info__info-image, .trend__info__info-image {
  width: 442px;
  height: 250px;
}

.card__info:last-child {
  margin-right: 0;
}

.hide {
  animation-name: hide;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}

.anime {
  transform: scale(0);
  opacity: 0;
}

@keyframes hide {
  0% {
    visibility: visible;
    height: 200px;
  }
  100% {
    height: 0;
    width: 0;
    max-width: 0;
    font-size: 0;
    margin: 0;
  }
}

.popular {
  width: 63px;
  height: 64px;
  border-top: 100px solid #8BC34A;
  border-right: 100px solid transparent;
  position: absolute;
}

.bolt {
  fill: #fff;
  width: 18px;
  height: 36px;
  position: relative;
  bottom: 86px;
  left: 14px;
}

.block {
  padding: 17px 31px;
  border-bottom: solid 1px #e7ecf2;
  align-items: center;
}

.block__price {
  font-weight: 700;
  font-size: 20px;
}

.block__text-title {
  font-size: 18px;
}

.block__text-category {
  color: #8BC34A;
}

.theme {
  padding: 17px 31px 21px;
}

.theme__author-link--img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.theme__author-link--name {
  color: #546e7a;
  margin-left: 13px;
}

.theme__author-link--name:hover {
  color: #8BC34A;
}

.trend {
  padding-bottom: 121px;
}

.trend__slider .slick-dots {
  margin-top: 67px;
  display: flex;
  justify-content: center;
}

.trend__slider .slick-dots li {
  list-style: none;
  margin: 0 7px;
}

.trend__slider .slick-dots li.slick-active button {
  background-color: #8BC34A;
}

.trend__slider .slick-dots button {
  font-size: 0;
  width: 16px;
  height: 16px;
  box-shadow: inset 1px rgba(154, 147, 140, 0.5);
  border-radius: 50%;
}

@media (max-width: 720px) {
  .container--fluid {
    padding: 0 15px;
  }
}
